import React, { Component } from "react"
import { Link } from "gatsby"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import { connect } from "react-redux";
import { userActions } from '../../redux/actions/';
import { Title, Subtitle, Table, Button } from 'bloomer';

class UserPage extends Component {

  constructor(props) {
    super(props);

    this.state = {
      list: []
    }

    this.handleDelete = this.handleDelete.bind(this)

  }

  handleDelete(user) {
    this.props.dispatch(userActions.remove(user.id))

  }

  componentDidMount() {
    this.props.dispatch(userActions.getAll())
  }

  static getDerivedStateFromProps(props, state) {
    let newState = state

    if (props.user.list){
      let list = props.user.list
      newState = {
        ...newState,
        list
      }
    }

    return newState
  }

  render () {
    const { list } = this.state

    return (
      <Layout>
        <SEO title="Menampilkan data user"/>
        <Title>User</Title>
        <Subtitle>Menampilkan data user</Subtitle>
        <Table isBordered isStriped isNarrow>
          <thead>
              <tr>
                  <th>Name</th>
                  <th>Email</th>
                  <th></th>
              </tr>
          </thead>
          <tbody>
          { list.length > 0 && list.map((u, index) =>
              <tr key={ index }>
                <td>{ u.name }</td>
                <td>{ u.email }</td>
                <td>
                <Link to={`/user/${u.id}/edit`} className="button is-light">
                  Edit
                </Link>&nbsp;
                <Button className="button is-danger" onClick={() => this.handleDelete(u)}>
                  Delete
                </Button>
                </td>
              </tr>
          ) }
          </tbody>
      </Table>
      </Layout>
    )
  }

}

const mapStateToProps = ({ user }) => {
  return { user }
}

export default connect(mapStateToProps)(UserPage);
